:root {
  --blackColor: #151515;
  --gradientFonColor: linear-gradient(180deg,
      rgba(196, 196, 196, 0) 25%,
      #bedbb0 92.19%);


  --transition: 0.4s;


  --poppinsFont: 'Poppins';


  --borderRadius8: 8px;


  --fontSize10: 8px;
  --fontSize10: 10px;
  --fontSize12: 12px;
  --fontSize14: 14px;
  --fontSize16: 16px;
  --fontSize18: 18px;
  --fontSize20: 20px;
  --fontSize28: 28px;
  --fontSize40: 40px;


  --fontWeight700: 700;
  --fontWeight600: 600;
  --fontWeight500: 500;
}


#root {
  padding: 0;
}


body {
  background-image: var(--gradientFonColor);
  background-repeat: no-repeat;
  margin: 0;
  width: 100%;
  height: 100vh;
  transition: color 0.5s ease, background-color 0.5s ease;
}


.dark-theme {
  --accentColor: #bedbb0;
  --headerColor: #161616;


  /*Sidebar*/
  --sidebarColor: #121212;
  --logo: #1F1F1F;
  --flash: #fff;
  --needHelpBg: #1f1f1f;
  --contrastColor: #ffffff;
  --plusBg: #BEDBB0;
  --myBoards: rgba(255, 255, 255, 0.5);
  --boarder: rgba(255, 255, 255, 0.1);
  --prOfBg: rgba(31, 31, 31, 1);
  --logOut: #fff;
  --logOutIcon: #bedbb0;


  /*Board*/
  --primaryBoardBg: #1f1f1f;
  --addBtnBox: #121212;
  --addBtnText: #fff;
  --filterTxt: rgba(255, 255, 255, 0.8);

  --filterModalTitle: rgba(255, 255, 255, 1);
  --filterLines: rgba(255, 255, 255, 0.1);
  --filterModalText:rgba(255, 255, 255, 0.5);
  --backgroundInput: rgba(22, 22, 22, 0.08);
  --withoutColor: rgba(255, 255, 255, 0.3);
  --lowColor: #8fa1d0;
  --mediumColor: #e09cb5;
  --highColor:rgba(190, 219, 176, 1);
  --defaultScreenColor: #e6e6e6;
  --calendarBackground: #1F1F1F;


 
  --opacityWhite: rgba(255, 255, 255, 0.3);
  --opacityWhite2: rgba(255, 255, 255, 0.5);
  --themeTitle: rgba(255, 255, 255, 0.8);
  --themeBgColor: #151515;
  --themeBoxShadow: rgba(17, 17, 17, 0.10);
  --themeItem: rgba(255, 255, 255, 0.5);
 


  --activeItemBorder: rgba(31, 31, 31, 1);
  --borderBottomColor: rgba(255, 255, 255, 0.1);
}


.light-theme {
  --accentColor: #bedbb0;
  --headerColor: #fcfcfc;


  /*Sidebar*/
  --sidebarColor: #ffffff;
  --logo: #1F1F1F;
  --flash: #ffffff;
  --needHelpBg: #f6f6f7;
  --contrastColor: #161616;
  --plusBg: #BEDBB0;
  --myBoards: rgba(22, 22, 22, 0.5);
  --boarder: rgba(22, 22, 22, 0.1);
  --prOfBg: #f6f6f7;
  --logOut: #161616;
  --logOutIcon: #bedbb0;


    /*Board*/
    --primaryBoardBg: #D9D9D9;
    --addBtnBox: #fff;
    --addBtnText: #161616;
    --filterTxt: rgba(22, 22, 22, 0.8);

  --filterModalTitle:rgba(22, 22, 22, 1);
  --filterLines: rgba(22, 22, 22, 0.1);
  --filterModalText:rgba(22, 22, 22, 0.5);
  --withoutColor:rgba(22, 22, 22, 0.3);
  --backgroundInput: rgba(22, 22, 22, 0.08);
  --lowColor: #8fa1d0;
  --mediumColor: #e09cb5;
  --highColor: rgba(190, 219, 176, 1);
  --defaultScreenColor: #e6e6e6;


  --opacityWhite: rgba(255, 255, 255, 0.3);
  --opacityWhite2: rgba(255, 255, 255, 0.5);
  --themeTitle: rgba(22, 22, 22, 0.8);
  --themeBgColor: #FCFCFC;
  --themeBoxShadow: rgba(17, 17, 17, 0.10);
  --themeItem: #161616;


  --activeItemBorder: rgba(246, 246, 247, 1);
  --borderBottomColor: rgba(22, 22, 22, 0.1);
}


.violet-theme {
  --accentColor: #5255bc;
  --headerColor: #ffffff;


  /*Sidebar*/
  --sidebarColor: #5255bc;
  --logo: #ECEDFD;
  --flash: #5255bc;
  --needHelpBg: rgba(236, 237, 253, 0.4);
  --contrastColor: #ffffff;
  --plusBg: #B8BCFD;
  --myBoards: rgba(255, 255, 255, 0.5);
  --boarder: rgba(255, 255, 255, 0.1);
  --prOfBg: rgba(255, 255, 255, 0.2);
  --logOut: #fff;
  --logOutIcon: #fff;


  /*Board*/
  --primaryBoardBg: #B8BCFD;
  --addBtnBox: #fff;
  --addBtnText: #161616;
  --filterTxt: rgba(22, 22, 22, 0.8);

  --filterModalTitle: rgba(22, 22, 22, 1);
  --filterLines:rgba(22, 22, 22, 0.1);
  --filterModalText:rgba(22, 22, 22, 0.5);
  --withoutColor: rgba(22, 22, 22, 0.3);
  --backgroundInput:rgba(22, 22, 22, 0.08);
  --lowColor: #8fa1d0;
  --mediumColor: #e09cb5;
  --highColor: rgba(190, 219, 176, 1);
  --defaultScreenColor: #e6e6e6;


 
  --opacityWhite: rgba(255, 255, 255, 0.3);
  --opacityWhite2: rgba(255, 255, 255, 0.5);
  --themeTitle: rgba(22, 22, 22, 0.8);
  --themeBgColor: #FCFCFC;
  --themeBoxShadow: rgba(17, 17, 17, 0.10);
  --themeItem: #161616;


  --activeItemBorder: rgba(255, 255, 255, 0.5);
  --borderBottomColor: rgba(255, 255, 255, 0.1);
}


h1,
h2,
h3,
p {
  margin: 0;
}


ul {
  margin: 0;
  padding: 0;
  list-style: none;
}


a {
  text-decoration: none;
  color: inherit;
}


* {
  box-sizing: border-box;
}
